.account-nav {
    width: calc(100% + 40px);
    margin-left: -20px;

    &.full {
        width: 100%;
        margin: 0;
    }

    &__list {
        padding: 0;
        margin: 0;
        margin-top: -9px;
        list-style: none;
        display: flex;

        border-bottom: 1px solid #ebebeb;

        .slick-list {
            padding-left: 20px;
            padding-bottom: 2px;
            margin-bottom: -2px;
        }
    }

    &__link {
        display: block;
        position: relative;
        padding: 12px 6px 3px;

        font-size: 12px;
        white-space: nowrap;
        color: $text-color;
        text-decoration: none;

        transition: color 300ms ease;

        &:hover,
        &:active,
        &:focus {
            color: $theme-color;

            text-decoration: none;
        }

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 2px;
            bottom: -1px;
            left: 0;

            transform: scaleX(0);

            background-color: $theme-color;

            transition: transform 300ms ease;
        }

        &.active {
            font-weight: 600;
        }

        &.active {
            &::before {
                transform: scaleX(1);
            }
        }

        &--notify {
            &::after {
                content: '!';
                position: absolute;
                top: 5px;
                right: 0px;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 9px;
                height: 9px;

                color: $text-color;
                line-height: 1;
                font-size: 7px;
                font-weight: 700;

                background-color: $theme-color;
                border-radius: 50%;
            }
        }
    }
}

@media #{$tablet} {
    .account-nav {
        width: 100%;
        margin: 0;

        &__item {
            flex: 1 1 auto;
        }

        &__link {
            padding: 5px 12px;

            font-size: 14px;
            text-align: center;

            &--notify {
                &::after {
                    right: 8px;
                }
            }
        }
    }
}

@media #{$desktop} {
    .account-nav {
        &__item {
            flex: 0 0 auto;
        }
        &__link {
            padding: 6px 32px 10px;

            &::after {
                top: 11px;
